.layout {
  padding-top: 40px;
  padding-bottom: 10px;
  @include desktop {
    display: grid;
    gap: 40px;
    grid-template-areas: 'sidebar content';
  }
  @include mobile {
    max-width: 375px;
  }
}
.books__item {
  @include tablet {
    margin-bottom: 40px;
  }
}
.home__category-container.container_active {
  display: block;
  @include tablet {
    max-height: 472px;
  }
  @include desktop {
    max-height: 472px;
    margin-bottom: 86px;
  }
}
.home-typeBook.container_active {
  display: block;
}
.category-item--all {
  display: inline-block;
}
.category-item,
.category-item--all {
  color: rgba(17, 17, 17, 0.6);
  cursor: pointer;
  transform: scale(0.97);
  transform-origin: center left;
}
.category-item:not(:last-child),
.category-item--all {
  margin-bottom: 24px;
  @include tablet {
    margin-bottom: 32px;
  }
  @include desktop {
    margin-bottom: 32px;
  }
  @include mobile {
    margin-bottom: 24px;
  }
}
.category-item--all:active,
.category-item--all:hover,
.category-item--all:focus,
.category-item.active,
.category-item:hover,
.category-item:focus {
  text-transform: uppercase;
  font-weight: 700;
  color: #4F2EE8;
  transform: scale(1);
}
.books__card-author {
  @include tablet {
    width: 218px;
  }
  @include desktop {
    width: 180px;
  }
}
.category__item {
  @include desktop {
    flex-basis: calc((100% - 100px) / 5);
  }
}
.categories-list {
  margin-bottom: 86px;
  width: 356px;
  height: 472px;
  gap: 20px;
  font-size: 18px;
  line-height: 1.33333;
  font-weight: 400;
  line-height: 1.125;
  display: block;
  overflow: auto;
  @include tablet {
    width: 337px;
  }
  @include mobile {
    height: 228px;
    width: 335px;
    font-size: 16px;
    margin-bottom: 40px;
  }
}
.categories-list::-webkit-scrollbar {
  width: 8px;
}
.categories-list::-webkit-scrollbar-track {
  background-color: transparent;
}
.categories-list::-webkit-scrollbar-thumb {
  background-color: #E0E0E0;
  border-radius: 4px;
}
.scategories-list::-webkit-scrollbar-thumb:hover {
  background-color: #B4AFAF;
}
.aside {
  display: flex;
  flex-direction: column;
  width: 360px;
  margin-left: 24px;
  @include tablet {
    flex-direction: row;
    gap: 32px;
    margin-left: 32px;
    margin-bottom: 40px;
    width: 100%;
    max-height: 472px;
  }
  @include desktop {
    flex-direction: column;
    max-width: 356px;
  }
  .dark-theme .category-item--all {
    color: rgba(255, 255, 255, 0.60)
  }
  .dark-theme .category-item {
    color: rgba(255, 255, 255, 0.60)
  }
  .dark-theme .category-item--all:active,
  .dark-theme .category-item--all:hover,
  .dark-theme .category-item--all:focus,
  .dark-theme .category-item.active,
  .dark-theme .category-item:hover,
  .dark-theme .category-item:focus
   {
    text-transform: uppercase;
    font-weight: 700;
    color: #EAC645;
    transform: scale(1);
  }
  @include mobile {
    margin-left: 20px;
  }
}