.menu-container.is-open {
  transform: translateX(0);
}

.menu-container {
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  position: fixed;
  margin-top: 66px;
  inset: 0;
  transform: translateX(100%);
  transition: transform 250ms;
  display: flex;
  width: auto;

  @include tablet {
    display: none;
  }
}

.mobile-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 48px;

  &_nav {
    display: flex;
    flex-direction: column;
    gap: 36px;
  }
  &_nav-item {
    color: white;
    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 18px;
    letter-spacing: -0.56px;
    text-transform: uppercase;
    display: flex;
    z-index: 0;
    position: absolute;
    justify-content: center;
    margin-left: 100px;
    text-align: center;
    margin-top: 160px;
    padding: 8px 16px;
  }
  & a:hover,
  & a:focus,
  & a:active {
    background-color: #eac645;
    border-radius: 36px;
    padding: 8px 16px;
    transition: background-color 0.3s;
    text-decoration: none;
    color: #111;
    fill: #111;
  }

  &__icon {
    fill: #fff;
    margin-left: 5px;
  }
}

.back-books {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 100%;
  z-index: 0;

  @include tablet {
    display: none;
  }
}

.log-out {
  color: #111;

  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: -0.56px;
  display: flex;
  position: absolute;
  bottom: 40;
  left: 30;
  margin: 0 auto;
  padding: 15px 24px;
  border-radius: 18px;
  border: 1.5px solid #111;
  background: #fff;

  &__arrow {
    margin-left: 5px;
  }
}
